import styled from "styled-components"

import SEO from "../components/seo"

import InfiniteSlider from "../components/infiniteslider"
import { relative } from "path";



let BackgroundImage = styled.img`
  position: fixed;
  z-index: -1;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 70%;
  margin: 0;
`
let BackgroundImage2 = styled.img`
  display: block;
  position: sticky;
  top: 50%; // required as well.
  transform: translateY(-50%);
  z-index: -1;
  width: 70%;
  margin: 450px auto 0;
  padding: 0
`

let ProjectDiv = styled.div`
  clip-path: 
  polygon(
    0% 2%,     /* top left */
    3% 0%,     /* top left */
    97% 0%,    /* top right */
    100% 2%,   /* top right */
    100% 100%,  /* bottom right */
    100% 100%,  /* bottom right */
    0% 100%,   /* bottom left */
    0 100%      /* bottom left */
  );
  position: relative;
  z-index: 990;
  height: 100%;
  width: 100%;
  top: 100vh;
  background-color: rgb(235,51,35);
  padding-top: 20px;
  // overflow-x: hidden;
`

let TriangleLeft = styled.div`
  position: relative;
`




class IndexPage extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      collections: [],
    }

  }

  componentWillMount(){
    console.log(this.props.data);

    let allCollections=[];

    let collectionFetchData = function(data) {
      this.projectName = "";
      this.projectYear = "";
      this.images = data;
    }

    // let collectionData = new collectionFetchData(this.props.data);

    // this.props.data.map((data) => {
    //   return allCollections.push(new collectionFetchData(data));
    // });

    allCollections = Object.values(this.props.data);

    this.setState({
      collections: allCollections
    });


  }

  componentDidMount(){

    console.log(this.state);

  }

  render(){

    return(
      <>
      <SEO title="Anna Genger" keywords={[`Anna`, `Genger`, `Projects`]} />
      <BackgroundImage src={Logo}/>
      <ProjectDiv>
        <BackgroundImage2 src={Logo}/>
        <InfiniteSlider data={this.state.collections}/>
        <InfiniteSlider data={this.state.collections}/>
        <InfiniteSlider data={this.state.collections}/>
        {/* <InfiniteSlider data={this.state.collections}/> */}
      </ProjectDiv>
    </>
    )
  }
}

export default IndexPage

